@import "normalize";
@import "lity";
@import "izimodal";
$button: #9c6ecc;
$color-primary:#9c6ecc;

body::-webkit-scrollbar {
    width: 1em;
}
 
body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

.uppercase-class {
  text-transform: uppercase;
}
html,body {
  height:100%;
}
body {
  font-family: 'Open Sans', sans-serif;
}
.mb-0 {
  margin:0;
}
.header {
  position: relative;
  color: #fff;
  background: url(../img/Girl_bg.jpg) center no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  margin-bottom: 73px;
  @media (min-width: 768px) {

  }
  .header-button-nav {
    span {
      &:nth-child(1) {
        top: 0px;
      }
    }
  }
  .active {
    transform:none;
    @media (max-width: 768px) {
      transform:translateX(100%);
    }
  }
  .header__nav {
    display: flex;
    justify-content:space-between;
    align-items:center;
    .header-login {
      border-radius: 5px;
      height: 40px;
      .login, .register {
        color: #fff;
        cursor: pointer;
        display: inline-block;
        padding: 9px 0;
        text-align: center;
        text-decoration: none;
      }
    }
    .header-logo {
      margin-top:34px;
      margin-left:60px;
    }

    .header-menu {
      position:relative;
      overflow: visible;
      margin-top:34px;
      margin-right:60px;
        @media (max-width: 768px) {
          display:none;
          display:block;
          margin:0;
          position: fixed;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: 1;
          background: #333;
          will-change:transform;
          transition-duration:0.2s;
        }
      .menu {
        display: flex;
        justify-content: flex-end;
        align-items:center;
        list-style-type: none;
        font-size:13px;
        @media (max-width: 768px) {
          flex-direction: column;
          margin: 0;
          padding: 0;
        }
        .menu-item {
          @media (max-width: 768px) {
            border-bottom:1px solid rgba(255,255,255,0.1);
            margin:0;
            width: 100%;
            padding-left:12px;
            .menu-link {
              display:block;
              padding: 0;
              margin: 0;
            }
          }
          margin-right:25px;
          a {
            color: #fff;
            text-decoration: none;
          }
        }
        .border-item {
          @media (max-width: 768px) {
            border:0;
          }
        }
        .menu-item-close {
          display:none;
          @media (max-width: 768px) {
          display:block;
          line-height:60px;
          text-align:right;
          width: 100%;
          border-bottom:1px solid rgba(255,255,255,0.1);          
            .close {
              color:#fff;
              text-decoration:none;
              padding-right:12px;
            }
          }
        }

        .border-item {
          border:1px solid white;
          border-radius:1px;
          .menu-link {
            display:inline-block;
            padding-top:15px;
            padding-bottom:16px;
            padding-left:42px;
            border-right:1px solid white;
            &:first-child{
              padding-right: 39px;
            }
            &:last-child {
              border:0;
              padding-right:33px;
            }
          }          
        }
      }
    }
    .header-button-nav {
      @media (max-width: 768px) {
          display:block;
        }
    }
  }
  .header__content {
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    margin-top:4%;
    padding-bottom:11%;
    @media (max-width: 768px) {
      align-items:center;
    }
    .header-container {
      text-align:left;
      margin-left:14%;
      @media (max-width: 768px) {
        margin:0;
        text-align:center;
      }
    }
    .header-text {
      font-size:40px;
      margin:0;
    }
    .bottom-text {
      font-size:18px;
      margin-bottom:50px;
    }
    .header-learn-btn {
      outline: none;
      border: 1px solid white;
      border-radius: 5px;
      background:transparent;
      color: #fff;
      cursor: pointer;
      font-size: .8rem;
      padding: 15px 37px;
      margin-right:18px;
    }
    .header-try-btn {
      background: #9c6ecc;
      border: 0;
      border-radius: 5px;
      color: #fff;
      cursor: pointer;
      font-size: .8rem;
      padding: 17px 25px;
    }
  }
}

.value {
  margin-bottom:100px;
  .value__grid {
    display: flex;
    padding-left:15px;
  }
  .value__head {
    display: flex;
    color: #4da2e3;
    font-size: 2rem;
    justify-content: center;
    margin-bottom: 69px;
    text-align:center;
  }
  .value__grid {
    max-width: 940px;
    margin: 0 auto;
    display:flex;
    flex-flow:row wrap;
    .value-item {
      display:flex;
      .value-img {
        margin-right:15px;
      }
      .value-text {
        line-height: 1.733;
        .sub {
          color:#888888;
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .value .value__grid .value-item {
    width: 50%;
  }
}

@media (min-width: 992px) {
  .value .value__grid .value-item {
    width: 33.33333%;
  }
}

@media (min-width: 1200px) {
  .value .value__grid .value-item {
    width: 33.33333%;
  }
}

.video {
  color: #fff;
  .video__content {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    background: url(../img/photo_overlay.jpg) center center no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    padding: 190px 0;
    .video-caption {
      text-align:center;
    }
  }
}

.trial {
  display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  margin-bottom: 135px;
  .trial__head {
    width:100%;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
    .trial-caption {
      text-align:center;
      margin-bottom: 60px;
      color: #4da2e3;
    }
    .trial__sub__text {
      text-align:center;
      color: #666;
      margin-bottom: 18px;
    }
  }
  .trial__contact {
    width: 100%;
    text-align: center;
    margin-bottom:43px;
    .contact-input {
      color: #666;
      background: #ece9e9;
      border: 0;
      padding: 2px 146px 2px 16px;
      height: 55px;
      margin-right: -4px;
    }
    .contact-button {
      display: inline-block;
      background: #9c6ecc;
      border: 0;
      color: #fff;
      cursor: pointer;
      font-size: .8rem;
      text-decoration: none;
      padding: 23px 20px;
    }
  }
  .trial__users {
    text-align: center;
    p {
      color:#888888;
    }
  }
}


.footer {
  width: 100%;
  background: #333;
  .footer__content {
    display: flex;
    flex-flow: row wrap;
    .footer-menu {
      display: flex;
      flex-flow: row wrap;
      color: #fff;
      list-style-type: none;
      margin: 0;
      padding: 25px;
      li {
        margin-right: 25px;
        a {
          color: #fff;
          text-decoration: none;
        }
      }
    }
  }
}

.header__form {
  position: relative;
  background: #fff;
  padding: 4em 4em 2em;
  margin: 12px auto 0;
  .form-caption {
    margin:0 0 50px 0;
    padding:10px;
    text-align:center;
    font-size:30px;
    color:darken(#e5e5e5, 50%);
    border-bottom:solid 1px #e5e5e5;
  }
  .form-element {
    margin: 0 0 2.5em 0;
    position: relative;
     &[type="radio"] {
       display:none;
     }
     input[type="file"] {
       margin-top:65px;
     }
     .form-element-label {
        position: relative;
        width: 270px;
        .form-label {
              position: absolute;
              top: -36px;
              font-size: 18px;
              color: #757575;
              pointer-events: none;
              transition-property:all;
              transition-duration:0.2s;
        }
     }
    .form-radio {
      &:checked + label {
      background-color: $color-primary;
      color:white;      
      }
    }
      .form-label {
        position: absolute;
        left: 8px;
        top: 12px;
        color: #999;
        font-size: 16px;
        display: inline-block;
        padding: 4px 10px;
        font-weight: 400;
        background-color: rgba(255,255,255,0);
        -webkit-transition: all 0.3s ease-in-out;
      }
      .label-gender {
        display:inline-block;
        width:50%;
      }
      .form-input {
        display: block;
        box-sizing: border-box;
        width: 100%;
        outline: none;
        margin:0;
        font-size: 18px;
        border: 0;
        border-bottom: 1px solid #757575;
        &:focus ~ .form-element-label .form-label {
              font-size:14px;
              transform:translateY(-20px);
        }
        &:valid ~ .form-element-label .form-label {
              font-size:14px;
              transform:translateY(-20px);
        }
      }
      .form-input[type="text"],
      .form-input[type="password"] {
          background: #fff;
          font-size: 1.6em;
          padding: 1.3em 0.3em 0.3em 0.3em;
          border-radius: 2px;
      }
      .form-title {
        display:block;
        background: #F9A5A5;
        padding: 2px 5px;
        color: #666;
      }
      .form-submit {
        display: block;
        width:100%;
        background: $button;
        border:0;
        color: #fff;
        cursor: pointer;        
        font-size: 2em;
        line-height: 1.6em;
        outline: none;
        text-shadow: 0 1px #68B25B;
      }
  }  
}

.header-button-nav {
  display:none;
  position: relative;
  border:0;
  background:transparent;
  outline:none;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
  padding-top: 15px;
  padding-right: 47px;
  margin-right: 29px;
  span {
      display: block;
      position: absolute;
      height: 9px;
      width: 100%;
      background: $button;
      border-radius: 9px;
      opacity: 1;
      left: 0;
      -webkit-transform: rotate(0deg);
      -moz-transform: rotate(0deg);
      -o-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: .25s ease-in-out;
      -moz-transition: .25s ease-in-out;
      -o-transition: .25s ease-in-out;
      transition: .25s ease-in-out;
  }
}
.header-button-nav span:nth-child(1) {
  top: 0px;
}

.header-button-nav span:nth-child(2),.header-button-nav span:nth-child(3) {
  top: 18px;
}

.header-button-nav span:nth-child(4) {
  top: 36px;
}

.header-button-nav.open span:nth-child(1) {
  top: 18px;
  width: 0%;
  left: 50%;
}

.header-button-nav.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.header-button-nav.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.header-button-nav.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}
